export async function init({ name, type }) {
  const { default: AdobeAn } = await import(`@/assets/js/${type}/${name}.js`);
  const canvas = document.getElementById('canvas');
  const animContainer = document.getElementById('animation_container');
  const domOverlayContainer = document.getElementById('dom_overlay_container');
  const comp = AdobeAn.composition;
  const lib = comp.getLibrary();
  const exportRoot = new lib._content();
  const stage = new lib.Stage(canvas);
  // Registers the "tick" event listener.
  const fnStartAnimation = () => {
    stage.addChild(exportRoot);
    window.createjs.Ticker.setFPS(lib.properties.fps);
    window.createjs.Ticker.addEventListener('tick', stage);
  };
  // Code to support hidpi screens and responsive scaling.
  AdobeAn.makeResponsive(
    true,
    'both',
    false,
    1,
    [canvas, animContainer, domOverlayContainer],
    stage
  );
  fnStartAnimation();
}
