<template>
  <div class="hw-container hw-modal">
    <nuxt-link
      :to="type === 'carrier' ? '/carriers' : '/'"
      class="close-button"
      flat
      icon
    >
      <icon name="close" size="34" />
    </nuxt-link>
    <div class="container-fluid">
      <h3 class="modal-heading">
        <span>Как</span>
        это работает?
      </h3>
      <div class="content">
        <nuxt-link
          icon
          :to="`/how-it-works/${type}/${index - 1}/`"
          :class="['swiper-modal-button prev', { disabled: index <= 1 }]"
        >
          <icon name="chevronLeft" size="44" />
        </nuxt-link>
        <div class="main-content">
          <div id="animation_container">
            <div id="dom_overlay_container" class="overlay-container">
              <canvas id="canvas" width="640" height="480" class="canvas" />
            </div>
          </div>
          <div class="text text--right">
            {{ text }}
            <plain-button
              v-if="showSignUpButton"
              class="md-raised gradient signup-btn"
              @click="
                $router.push({
                  path: '/carriers',
                  query: { show_signup: true }
                })
              "
            >
              Зарегистрироваться
            </plain-button>
          </div>
        </div>
        <nuxt-link
          icon
          :to="`/how-it-works/${type}/${index + 1}/`"
          :class="['swiper-modal-button next', { disabled: index >= total }]"
        >
          <icon name="chevronRight" size="44" />
        </nuxt-link>
      </div>
      <div class="text text--bottom">
        <p class="text-content">
          {{ text }}
        </p>
        <plain-button
          v-if="showSignUpButton"
          class="md-raised gradient signup-btn"
          @click="
            $router.push({ path: '/carriers', query: { show_signup: true } })
          "
        >
          Зарегистрироваться
        </plain-button>
      </div>
      <div class="pagination">
        <div v-for="i in total" :key="i" class="pagination-item">
          <nuxt-link
            :to="`/how-it-works/${type}/${i}/`"
            class="point"
            :class="{ active: i === index }"
          >
            {{ i }}
          </nuxt-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Icon from 'common/components/Icon.vue';
import { init } from '@/assets/js/animHelper';
import getTextMap from '@/assets/enums/texts';

export default {
  head() {
    return {
      title: `Как работает сервис MoreCargo для ${
        this.$route.params.type === 'carrier'
          ? 'перевозчика'
          : 'грузоотправителя'
      } - этап ${this.$route.params.index} из ${
        this.textMap.filter(_ => this.$route.params.type in _).length
      }`,
      meta: [
        { name: 'url', content: 'https://morecargo.ru/docs' },
        { name: 'robots', content: 'noindex, nofollow' },
        {
          name: 'description',
          content: `Основные правила работы с сервисом MoreCargo для ${
            this.$route.params.type === 'carrier'
              ? 'грузоперевозчика, водителя'
              : 'грузовладельца, отправителя груза'
          }`
        }
      ]
    };
  },
  name: 'HowItWorks',
  middleware({
    route: {
      params: { index, type }
    },
    redirect,
    store
  }) {
    const { bonusPrivelege } = store.state.settings;
    const textMap = getTextMap(bonusPrivelege);
    const total = textMap.filter(_ => type in _).length;
    if (!index || !['carrier', 'shipper'].includes(type)) return redirect('/');
    if (index > total || index <= 0) return redirect(`/how-it-works/${type}/1`);
  },
  components: {
    Icon
  },
  computed: {
    ...mapState({
      bonusPrivelege: state => state.settings.bonusPrivelege
    }),

    textMap() {
      return getTextMap(this.bonusPrivelege);
    },

    text() {
      return this.textMap[this.index - 1]?.[this.type];
    },
    showSignUpButton() {
      return this.index === this.total && this.type === 'carrier';
    }
  },
  created() {
    this.index = Number(this.$route.params.index);
    this.type = this.$route.params.type;
    this.total = this.textMap.filter(_ => this.$route.params.type in _).length;
  },
  mounted() {
    if (!window.createjs) {
      require('@/assets/js/createjs.js');
      const interval = setInterval(() => {
        if (window.createjs) {
          init({ name: `0${this.index}`, type: this.type });
          clearInterval(interval);
        }
      }, 500);
    } else {
      init({ name: `0${this.index}`, type: this.type });
    }
  }
};
</script>

